"use client";

import { useState } from "react";
import { LogotypeVertical } from "../Logotype/LogotypeVertical";
import { LogotypeHorizontal } from "../Logotype/LogotypeHorizontal";
import { HamburgerButton } from "./HamburgerButton/HamburgerButton";
import { MobileMenu } from "./MobileMenu/MobileMenu";
import cls from "./Header.module.scss";

const scrollToSection = (sectionId: string) => {
  const element = document.getElementById(sectionId);
  if (element) {
    element.scrollIntoView({ behavior: "smooth", block: "start" });
  }
};

export const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const handleMenuToggle = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleNavClick = (sectionId: string) => {
    scrollToSection(sectionId);
    setIsMenuOpen(false);
  };

  return (
    <header className={cls.header}>
      <nav className={cls.navigation}>
        {/* Desktop Navigation */}
        <div className={cls.desktopNav}>
          <button
            className={cls.navigationButton}
            onClick={() => scrollToSection("catalog")}
          >
            Каталог
          </button>
          <button
            className={cls.navigationButton}
            onClick={() => scrollToSection("about")}
          >
            О компании
          </button>

          <LogotypeVertical
            width={136}
            height={57}
            className={cls.logotype}
          />

          <button
            className={cls.navigationButton}
            onClick={() => scrollToSection("contacts")}
          >
            Контакты
          </button>
          <button
            className={cls.navigationButton}
            onClick={() => scrollToSection("catalog")}
          >
            Скачать каталог
          </button>
        </div>

        {/* Mobile Navigation */}
        <div className={cls.mobileNav}>
          <LogotypeHorizontal
            width={120}
            height={24}
            className={cls.mobileLogotype}
          />
          <HamburgerButton
            onClick={handleMenuToggle}
            isOpen={isMenuOpen}
          />
        </div>
      </nav>

      <MobileMenu
        isOpen={isMenuOpen}
        onClose={() => setIsMenuOpen(false)}
        onNavClick={handleNavClick}
      />
    </header>
  );
};